.footer {
  background-color: #f1f1f1;

  .footer__wrapper {
    width: 320px;
    margin: 0 auto;
  }

  .footer__social {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0;
    margin: 0 30px;

    border-bottom: 1px solid #e5e5e5;
  }

  .footer__social-icon {
    margin: 0 20px;
  }

  .footer__social-inst {
    width: 22px;
    height: 21px;
    margin: 0 20px;

    background-image: url("../img/icon-insta.svg");
    background-repeat: no-repeat;
  }

  .footer__social-fb {
    width: 15px;
    height: 28px;
    margin: 0 20px;

    background-image: url("../img/icon-fb.svg");
    background-repeat: no-repeat;
  }

  .footer__social-twit {
    width: 23px;
    height: 20px;
    margin: 0 20px;

    background-image: url("../img/icon-twitter.svg");
    background-repeat: no-repeat;
  }

  .footer__maker {
    position: relative;
    width: 182px;
    margin: 0 auto;
  }

  .footer__maker-text {
    margin: 0;
    padding: 30px 0;

    font-family: "opensansbold", "Arial", sans-serif;
    font-weight: bold;
    font-size: 19px;
    line-height: 18px;
    text-align: left;
    color: #000000;

    &--icon-academy {
      &::after {
        content: "";
        position: absolute;
        top: 25px;
        right: 0;

        width: 27px;
        height: 34px;

        background-image: url("../img/htmlacademy.svg");
        background-repeat: no-repeat;
      }
    }
  }
}
